label {
  margin-bottom: 0.25rem;
  letter-spacing: 0.05em;
}
input[type=checkbox]:checked {
  --tw-text-opacity: 1;
  color: rgb(51 51 51 / var(--tw-text-opacity));
}
input[type=checkbox] {
  border-radius: 0.25rem;
}
input[type=checkbox]:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
  --tw-ring-color: transparent;
}
.Modal-addBlocks {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  height: max-content;
  width: 60%;
  border-radius: 8px;
  background-color: white;
}
.Modal-htmlWarning {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  height: max-content;
  width: 25%;
  border-radius: 8px;
  background-color: white;
}
.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color : rgba(0,0,0,0.5);
}
@media screen and (max-width: 1500px) {
  .Modal-htmlWarning {
    width: 30%;
  } 
}
@media screen and (max-width: 1280px) {
  .Modal-htmlWarning {
    width: 40%;
  } 
}
@media screen and (max-width: 1024px) {
  .Modal-htmlWarning {
    width: 50%;
  } 
}
@media screen and (max-width: 768px) {
  .Modal-addBlocks {
    width: 90%;
    height: 50%;
    overflow: scroll;
  }

  .Modal-htmlWarning {
    width: 60%;
    overflow: scroll;
  } 
}
@media screen and (max-width: 640px) {
  .Modal-htmlWarning {
    width: 80%;
    overflow: scroll;
  } 
}
